	module.exports = {"data":{"post":{"html":"<p>Six pm in Germany on NDR, Monday through Thursday, in households with small children it's time for <a href=\"http://www.ndr.de/tv/sesamstrasse/\">Sesamstrasse</a>, the German version of Sesame Street. Christopher is now old enough to follow along, so I try to arrange our afternoon schedule so he can watch. The street scenes are made in Hamburg (featuring Samson the bear instead of Big Bird, sometimes with <a href=\"http://us.imdb.com/Name?S%E4gebrecht,+Marianne\">Marianne Sägebrecht</a> of <a href=\"http://us.imdb.com/Title?0095801\">Bagdad Cafe</a> fame), and the segments seem to be about 50-50 German/American.</p>","fields":{"title":"Der Die Das! Wer Wie Was!","date":"15 Jul 2002","url":"https://papascott.de/2002-07-15-der-die-das-wer-wie-was/"}}},"pathContext":{"slug":"/2002-07-15-der-die-das-wer-wie-was/","prev":{"fields":{"title":"Sunlog and uni.tb","date":"14 Jul 2002","slug":"/2002-07-14-sunlog-and-unitb/"}},"next":{"fields":{"title":"Bye Bye Ron Sommer","date":"16 Jul 2002","slug":"/2002-07-16-bye-bye-ron-sommer/"}}}}

